body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.post-detail {
  padding: 10px;
  background-color:thistle;
}

.post-detail > *:not(:last-child) {
  margin-bottom: 10px;
}
.post-list {
  display: grid;
  grid-template-columns: 200px 200px;
  grid-gap: 10px;
  padding: 10px;
  background-color: mediumaquamarine;
}

/* .post-list > *:not(:last-child) {
  margin-bottom: 10px;
} */
.app {
  display: grid;
  min-height: 100vh;
  grid-template-columns: auto 1fr;
  grid-template-areas: 
  "header    header"
  "post-list post";
  align-content: flex-start;
  background-color: aliceblue;
}

.app__title {
  grid-area: header;
  padding: 10px 20px;
  font-size: 40px;
  font-weight: bold;
  text-transform: uppercase;
}

.app__post-list {
  grid-area: post-list;
  align-self: start;
}

.app__post-detail {
  grid-area: post;
  align-self: start;
}
.author {
  font-size: 18px;
}

.author-name {
  font-weight: bold;
}

.author-email {
  font-style: italic;
}
.comment {
  padding: 10px;
  margin: unset;
  line-height: 1.5;
  background-color: lavender;
}

.comment-title {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 18px;
}

.comment-body {
  margin: unset;
  font-size: 18px;
}

.comment_author {
  margin-top: 10px;
}
.comment-list > *:not(:last-child) {
  margin-bottom: 10px;
}
.post {
  padding: 10px;
  background-color: azure;
}

.post-title {
  margin: unset;
  text-transform: uppercase;
  font-size: 24px;
}

.post-body {
  margin: unset;
  line-height: 1.5;
  font-size: 18px;
}

.post_author {
  margin-top: 25px;
}
.post-preview { 
  -webkit-transition: background-color .075s; 
  transition: background-color .075s;
  padding: 10px;
  color: unset;
  background-color: khaki;
  text-decoration: unset;
  cursor: pointer;
}

.post-preview:hover {
  background-color: #FFF8BA;
}

.post-preview-title {
  margin-top: unset;
  margin-bottom: 15px;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 1.25;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.post-preview-body {
  margin: unset;
  line-height: 1.5;
  font-size: 18px;
  text-align: justify;
  overflow-y: hidden;
  height: 135px;
}
.more-button {
  position: relative;
  width: 200px;
  height: 200px;
  -webkit-transition: background-color .075s;
  transition: background-color .075s;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  background-color: paleturquoise;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.more-button__title {
  position: absolute;
  right: 0;
  left: 0;
}

.more-button__img {
  margin: 50px;
}

.more-button:hover {
  background-color: #D0FDFD;
}
.reset-button {
  position: relative;
  width: 200px;
  height: 200px;
  -webkit-transition: background-color .075s;
  transition: background-color .075s;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  background-color: coral;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.reset-button__title {
  position: absolute;
  right: 0;
  left: 0;
}

.reset-button__img {
  margin: 50px;
}

.reset-button:hover {
  background-color: #FF9973;
}
* {
  box-sizing: border-box;
}
